import React, { useEffect, useState } from 'react';
import OTPInput from 'react-otp-input';
import { CustomButton, CustomError, CustomSpinner } from '../../globalComponents';
import { Button } from 'react-bootstrap';

const AadhaarOTPModal = (props) => {
    const { minutes, seconds, generateAadharOTPbtn, clientIdGetFormApi, setUserVerifiedDetails, onClickActive, aadharNumber, updateAadharVerify } = props;
    const [otp, setOtp] = useState('');
    const [loader, setLoader] = useState(false);
    const [errors, setErrors] = useState({});

    const handleChangeOTP = otp => {
        setOtp(otp)
    };

    // aadhar  otp verify api
    const verifyAadharOTPbtn = async () => {
        setLoader(true);
        let otpNumbersConcate = otp;
        let data = {
            "client_id": clientIdGetFormApi,
            "otp": otpNumbersConcate
        }
        const response = await fetch("https://kyc-api.surepass.io/api/v1/aadhaar-v2/submit-otp", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Authorization": "Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJmcmVzaCI6ZmFsc2UsImlhdCI6MTYzNTMzMzk2NywianRpIjoiMTQzNmY1MjQtMWY3Zi00MjQ2LWIzODgtYzY5MmRkZWI4NGE2IiwidHlwZSI6ImFjY2VzcyIsImlkZW50aXR5IjoiZGV2LnFkZWdyZWVzQGFhZGhhYXJhcGkuaW8iLCJuYmYiOjE2MzUzMzM5NjcsImV4cCI6MTk1MDY5Mzk2NywidXNlcl9jbGFpbXMiOnsic2NvcGVzIjpbInJlYWQiXX19.MaT1P2eR31wpoXKBwjG8yJkSmDGHzxDFb0zRCDaj7qg"
            },
            body: JSON.stringify(data)
        }).catch((error) => {
        });

        let result = await response.json();
        if (result && result.success == true) {
            setLoader(false)
            setUserVerifiedDetails(result.data);
            onClickActive("personal_details")
            updateAadharVerify(true)
            setUserVerifiedDetails(prevData => ({
                ...prevData,
                aadhaar_no: aadharNumber,
              }));
        } else {
            setLoader(false)
            setErrors({ message: result && result?.message })
        }
    };


    return (
        <div className="rightSec">
            <div className="formPanel detailInner">
                <div className="DetailHeader">
                    <div>
                        <h6 className="m-0 fw-semibold">Aadhaar Verification OTP</h6>
                        <small>Please enter <b className="text-danger">OTP</b> received on your <b className="text-danger">Mobile Number</b> and click on verify button</small>
                    </div>
                    <div className="position-relative">
                        <div style={{ width: 70, height: 70 }}>
                        </div>
                    </div>
                </div>

                <div className="text-center mb-4">
                    <div className="text-center" style={{ padding: "20px" }}>
                        <img src="images/password.png" alt="OTP" width="100" />
                    </div>
                    <div>
                        <div>
                            <OTPInput
                                value={otp}
                                onChange={handleChangeOTP}
                                numInputs={6}
                                isInputNum={true}
                                shouldAutoFocus={true}
                                containerStyle={{margin:'auto',display:'inline-flex'}}
                                inputStyle={{
                                    width: '2.5rem',
                                    height: '2.5rem',
                                    margin: '0 2px',
                                    fontSize: '16px',
                                    borderRadius: 4,
                                    border: '1px solid #ced4da',
                                    textAlign: 'center'
                                }}
                                renderInput={(props) => <input {...props} />}
                            />
                        </div>
                        {errors.message && <CustomError
                            text={errors.message}
                        />}
                    </div>
                    <div style={{ padding: "20px", display: "inline-flex", gap: "5px" }}>
                        <CustomButton
                            text="Verify"
                            classNames="btn btn-success"
                            handleClick={() => verifyAadharOTPbtn()}
                            isLoading={loader}
                        />
                        <CustomButton
                            text={(seconds > 0 || minutes > 0) ? <> {minutes < 10 ? `0${minutes}` : minutes}:  {seconds < 10 ? `0${seconds}` : seconds}</> : "Resend OTP"}
                            classNames="btn btn-danger"
                            handleClick={(seconds > 0 || minutes > 0) ? null : generateAadharOTPbtn()}
                        />
                    </div>
                </div>
            </div>
        </div>
    );
};

export default AadhaarOTPModal;
