import React, { useEffect, useState } from 'react'
import { Icon, Input, Label } from '../../commanComponent'
import { CustomButton, CustomError } from '../../globalComponents';
import EducationDetailsTable from './EducationDetailsTable';
import ApproveRejectModal from '../../modal/ApproveRejectModal';
import { useCustomTranslation } from '../../../translation';
import { validateSchema } from '../../../utils/ValidationHelper';
import { addEducationDetailsSchema } from '../../../utils/ValidationSchema';
import { CircularProgressbar } from 'react-circular-progressbar';
import { calculateFilledPercentage } from '../../../utils/common';

const EducationDetails = (props) => {
  const { onClickActive, addEductionDetails, profileData, getPreviousObjectKeyValue, modeProfileModals } = props
  const { t } = useCustomTranslation()
  const [educationDetails, setEductionDetail] = useState(
    {
      educationalClass: '',
      rollNo: '',
      schoolName: '',
      board: '',
      percentage: '',
      year: '',

    }
  )
  const [tempEducationDetails, setTempEductionDetail] = useState([])
  const [isShowDeleteModal, setIsShowDeleteModal] = useState(false)
  const [deleteIndex, setDeleteIndex] = useState('')
  const [errors, setErrors] = useState({})

  const percentage = calculateFilledPercentage(educationDetails);
  const handleFromChange = (input, fieldName) => {
    const name = fieldName || input.target.name;
    const value = input.target ? input.target.value : input;

    setEductionDetail((prevData) => {
      let updatedData = {
        ...prevData,
        [name]: value,
      };
      return updatedData;
    });
    setErrors(prevErrors => ({
      ...prevErrors,
      [name]: undefined,
    }));

  };

  useEffect(() => {
    if (profileData) {
      setTempEductionDetail(profileData?.education_details)
    }
  }, [profileData])
  const onSaveAndAddForm = async () => {
    const validationResponse = await validateSchema(addEducationDetailsSchema, educationDetails);
    if (validationResponse.isValid) {
      setEductionDetail(
        {
          educationalClass: '',
          rollNo: '',
          schoolName: '',
          board: '',
          percentage: '',
          year: '',
        },
      );
      setTempEductionDetail((prevData) => [...prevData, educationDetails])
    }
    else {
      setErrors(validationResponse.errors);
    }
  };

  const onDeleteEducationDelete = (type) => {
    if (type) {
      const updatedEducationDetail = tempEducationDetails.filter((_, i) => i !== deleteIndex);
      setTempEductionDetail(updatedEducationDetail);
      setDeleteIndex('')
    }
    toggleDeleteEducationModal()
  };
  const onSubmitForm = () => {
    const params = {
      education_details: tempEducationDetails
    }
    addEductionDetails(params)
    // onClickActive(4)
  }

  const renderEmployeeDetails = () => {
    return <div>
      <h6>Details {tempEducationDetails?.length + 1}</h6>
      <div className="p-3">
        <div className="row">
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={"Educational Class"} required={true} />
              <Input
                type="text"
                placeholder={"Please Enter Educational Class"}
                value={educationDetails?.educationalClass}
                name="educationalClass"
                onChange={(e) => handleFromChange(e)}
                isDisabled={modeProfileModals}
              />
              {errors?.educationalClass && (
                <CustomError text={errors.educationalClass} />
              )}
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={"Roll No"} required={true} />
              <Input
                type="number"
                placeholder={"Please Enter roll no"}
                value={educationDetails?.rollNo}
                name="rollNo"
                onChange={(e) => handleFromChange(e)}
                isDisabled={modeProfileModals}
              />
              {errors?.rollNo && (
                <CustomError text={errors.rollNo} />
              )}
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={"School Name"} required={true} />
              <Input
                type="text"
                placeholder={"Please Enter School Name"}
                value={educationDetails?.schoolName}
                name="schoolName"
                onChange={(e) => handleFromChange(e)}
                isDisabled={modeProfileModals}
              />
              {errors?.schoolName && (
                <CustomError text={errors.schoolName} />
              )}
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={"School/College Board"} required={true} />
              <Input
                type="text"
                placeholder={"Please Enter School/College Board"}
                value={educationDetails?.board}
                name="board"
                onChange={(e) => handleFromChange(e)}
                isDisabled={modeProfileModals}
              />
              {errors?.board && (
                <CustomError text={errors.board} />
              )}
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={"Percentage"} required={true} />
              <Input
                type="number"
                placeholder={"Please Enter Percentage"}
                value={educationDetails?.percentage}
                name="percentage"
                onChange={(e) => handleFromChange(e)}
                isDisabled={modeProfileModals}
              />
              {errors?.percentage && (
                <CustomError text={errors.percentage} />
              )}
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={"Year"} required={true} />
              <Input
                type="month"
                placeholder={"Please Enter Year"}
                value={educationDetails?.year}
                name="year"
                onChange={(e) => handleFromChange(e)}
                isDisabled={modeProfileModals}
              />
              {errors?.year && (
                <CustomError text={errors.year} />
              )}
            </div>
          </div>
        </div>
      </div>

    {modeProfileModals!==true &&  <div className="d-flex justify-content-end">
        <CustomButton
          text="Save & Add More"
          classNames="btn btn-primary me-2"
          handleClick={() => onSaveAndAddForm()}
          icon={<Icon className={"fa fa-plus"} />}
        />
      </div>}

      {  <div className='d-flex mt-3 mb-3 p-3' style={{ justifyContent: "space-between" }}>
       {modeProfileModals!==true && modeProfileModals!==false  && <div>
          <CustomButton
            text="Back"
            classNames="btn btn-org-gradient"
            handleClick={() => getPreviousObjectKeyValue("short_name", "education_details")}

          />
        </div>}
       {modeProfileModals!==true &&  <div >
          <CustomButton
             text={modeProfileModals !== true?"Save":"Save & Next"}
            classNames="btn btn-org-gradient"
            isDisabled={!(tempEducationDetails?.length > 0)}
            handleClick={() => onSubmitForm(4)}
          />
        </div>}
      </div>}
    </div>
  }

  const toggleDeleteEducationModal = (index) => {
    setIsShowDeleteModal(!isShowDeleteModal)
    setDeleteIndex(index)
  }

  return (
    <>
      <div className={modeProfileModals!==true && modeProfileModals!==false ? `rightSec` : ''}>
        <div className={`formPanel detailInner`}>
          {modeProfileModals!==true && modeProfileModals!==false  &&<div className="DetailHeader">
            <div>
              <h6 className="m-0 fw-semibold">Education Details</h6>
              <small>Please fill all education details to proceed to the next step.</small>
            </div>
            <div className="position-relative">
              <div style={{ width: 70, height: 70 }}>
                <CircularProgressbar value={percentage} text={`${percentage}%`} styles={{ path: { stroke: "green" } }} />;
              </div>
            </div>
          </div>}
          <div className='rightSec'>
            {tempEducationDetails && tempEducationDetails?.length > 0 &&
              <EducationDetailsTable
                empDetailData={tempEducationDetails}
                toggleDeleteEducationModal={toggleDeleteEducationModal}
                modeProfileModals={modeProfileModals}
              />
            }
          </div>


          {renderEmployeeDetails()}

        </div>
      </div>

      {isShowDeleteModal && (
        <ApproveRejectModal
          onApproveReject={onDeleteEducationDelete}
          isShow={isShowDeleteModal}
          desc={"You want to delete"}
          btnTextRight={t("btns.delete")}
          btnTextLeft={t("btns.cancel")}
        />)
      }
    </>
  )
}

export default EducationDetails