import React, { useState } from 'react'
import Onboarding from '../../../view/Onboarding'

const OnboardingSidebar = (props) => {
    const { onClickActive, isActive, filledFormTabs,allOnboardingSteps } = props;

    const renderOnboardingSteps = () => {
        return (
            allOnboardingSteps&& allOnboardingSteps?.length >0&& allOnboardingSteps?.map((item, index) => 
                (<li onClick={() => onClickActive(item?.short_name)} key={index}>
                    <span className={`details ${isActive === item?.short_name ? "active":''} ${filledFormTabs.includes(item?.short_name) ? "selected":''}`}>
                        <figure className="mb-0">
                            <img src="images/green-rightdet.svg" alt="" width={36} />
                            <b>{index+1}</b>
                        </figure>
                        <div className="detailsTxt">
                            <span>{item?.name}</span>
                            <p>{item.description}</p>
                        </div>
                    </span>
                </li>)
            )
        )
    }

    return (

        <div className="col-md-3">
            <div className="leftSide">
                <ul>
                    {renderOnboardingSteps()}
                </ul>
            </div>
        </div>



    )
}

export default OnboardingSidebar