import React, { useMemo } from 'react'
import { CustomTable } from '../../reuseableComponents'
import moment from 'moment';
import { Icon } from '../../commanComponent';

const EducationDetailsTable = (props) => {
  const { empDetailData, toggleDeleteEducationModal,modeProfileModals } = props
  const fields = [
    { key: 'sn', label: 'S.N' },
    { key: 'educationalClass', label: 'Educational Class' },
    { key: 'rollNo', label: 'Roll No' },
    { key: 'percentage', label: 'Percentage' },
    { key: 'year', label: 'Year' },
    { key: 'action', label: 'Action' }
  ];

  const scopedSlots = useMemo(() => ({
    sn: (_, index) => (
      <span >{index + 1}</span>
    ),

    year: item => (
      <span> 
        {item.year?moment(item.year).format("MM-YYYY"):"-"}
      </span>),
    action: (_, index) => (
      <>
        {!modeProfileModals &&<span onClick={() => toggleDeleteEducationModal(index)}>
          <Icon
            className={"fa fa-trash text-danger"}
            style={{ cursor: 'pointer' }}
          />
        </span>}
      </>
    )
  }), [empDetailData]);
  return (
    <>
      <CustomTable
        data={empDetailData}
        fields={fields}
        pagination={true}
        tableName={"Employee Details"}
        scopedSlots={scopedSlots}
        isHeader={false}
      />
    </>
  )
}

export default EducationDetailsTable
