import React, { useEffect, useState } from 'react';
import OTPInput from 'react-otp-input';
import { CustomButton, CustomError, CustomSpinner } from '../../globalComponents';
import { Button } from 'react-bootstrap';
import { notificationActions } from '../../../core/resource/actions';
import AadhaarOTPModal from './AadhaarOTPModal';

const AadharVerifyModal = (props) => {
    const { setUserVerifiedDetails, onClickActive, updateAadharVerify } = props;
    const regexAadhar = /^[2-9]{1}[0-9]{3}[0-9]{4}[0-9]{4}$/;
    const [aadharNumber, setAadharNumber] = useState('');
    const [button, setButton] = useState(true);
    const [errors, setErrors] = useState({});
    const [minutes, setMinutes] = useState(0);
    const [seconds, setSeconds] = useState(0);
    const [clientIdGetFormApi, setClientIdGetFormApi] = useState('');
    const [loader, setLoader] = useState(false);

    useEffect(() => {
        const interval = setInterval(() => {
            if (seconds > 0) {
                setSeconds(seconds - 1);
            }

            if (seconds === 0) {
                if (minutes === 0) {
                    clearInterval(interval);
                } else {
                    setSeconds(59);
                    setMinutes(minutes - 1);
                }
            }
        }, 1000);

        return () => {
            clearInterval(interval);
        };
    });

    const checkOtp = e => {
        setAadharNumber(e);
        if (e.length === 12) {
            if (regexAadhar.test(e)) {
                setButton(false);
                setErrors({})
            } else {
                setErrors({ message: "Please enter a valid aadhaar no." })
                setButton(true);
            }
        } else {
            setButton(true);
            setErrors({})
        }
    };

    const generateAadharOTPbtn = async () => {
        setLoader(true)
        if (regexAadhar.test(aadharNumber)) {
        } else {
            setErrors({message: "Please enter a valid aadhaar no." })
            setLoader(false)
        }
        let data = {
            "id_number": aadharNumber
        }
        const response = await fetch("https://kyc-api.surepass.io/api/v1/aadhaar-v2/generate-otp", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Authorization": "Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJmcmVzaCI6ZmFsc2UsImlhdCI6MTYzNTMzMzk2NywianRpIjoiMTQzNmY1MjQtMWY3Zi00MjQ2LWIzODgtYzY5MmRkZWI4NGE2IiwidHlwZSI6ImFjY2VzcyIsImlkZW50aXR5IjoiZGV2LnFkZWdyZWVzQGFhZGhhYXJhcGkuaW8iLCJuYmYiOjE2MzUzMzM5NjcsImV4cCI6MTk1MDY5Mzk2NywidXNlcl9jbGFpbXMiOnsic2NvcGVzIjpbInJlYWQiXX19.MaT1P2eR31wpoXKBwjG8yJkSmDGHzxDFb0zRCDaj7qg"
            },
            body: JSON.stringify(data)
        }).catch((error) => {
        });
        let result = await response.json();
        if (result?.data?.valid_aadhaar === false && result?.data?.status === "invalid_aadhaar") {
            setErrors({ message: "Please enter a valid aadhaar no." })
            setLoader(false)
        }
        if (result && result?.data?.if_number == true && result?.data?.otp_sent == true) {
            setLoader(false)
            setClientIdGetFormApi(result?.data?.client_id);
            setMinutes(1)
            setSeconds(59)  
        }
    };

    return (
        <>
            {!clientIdGetFormApi ?
                <div className="rightSec">
                    <div className="formPanel detailInner">
                        <div className="DetailHeader">
                            <div>
                                <h6 className="m-0 fw-semibold">Aadhaar Verification Number</h6>
                                <small>  You need to enter your Aadhaar number and click on proceed
                                    button to verify your aadhaar</small>
                            </div>
                            <div className="position-relative">
                                <div style={{ width: 70, height: 70 }}>
                                </div>
                            </div>
                        </div>

                        <div className="text-center mb-4">
                            <div className="text-center" style={{ padding: "20px" }}>
                                <img src="images/aadhar.svg" alt="Aadhaar Logo" width="100" />
                            </div>
                            <div>
                                <label style={{ fontSize: "15px" }}>Aadhaar Number</label>
                                <div>
                                    <OTPInput
                                        value={aadharNumber}
                                        onChange={(e) => checkOtp(e)}
                                        numInputs={12}
                                        isInputNum={true}
                                        shouldAutoFocus={true}
                                        containerStyle={{margin:'auto',display:'inline-flex'}}
                                        inputStyle={{
                                            width: '2.5rem',
                                            height: '2.5rem',
                                            margin: '0 2px',
                                            fontSize: '16px',
                                            borderRadius: 4,
                                            border: '1px solid #ced4da',
                                            textAlign: 'center'
                                        }}
                                        renderInput={(props) => <input {...props} />}
                                    />
                                </div>
                                {errors.message && <CustomError
                                    text={errors.message}
                                />}
                            </div>
                            <div style={{ padding: "20px", display: "inline-block" }}>
                                <CustomButton
                                    text="Proceed"
                                    classNames="btn btn-org-gradient" 
                                    isDisabled={button}
                                    handleClick={()=> generateAadharOTPbtn()}
                                    isLoading={loader}
                                />
                            </div>
                        </div>
                    </div>
                </div>
                :
                <AadhaarOTPModal 
                seconds={seconds} 
                minutes={minutes} 
                generateAadharOTPbtn={generateAadharOTPbtn} 
                clientIdGetFormApi={clientIdGetFormApi} 
                setUserVerifiedDetails={setUserVerifiedDetails} 
                onClickActive={onClickActive} 
                aadharNumber={aadharNumber}
                updateAadharVerify={updateAadharVerify}
                />
            }
        </>
    );
};

export default AadharVerifyModal;
