import React, { useEffect, useState } from 'react'
import { Icon, Input, Label } from '../../commanComponent'
import { CustomButton } from '../../globalComponents';
import WorkExperienceDetailsTable from './WorkExperienceDetailsTable';
import ApproveRejectModal from '../../modal/ApproveRejectModal';
import { useCustomTranslation } from '../../../translation';
import CustomSelect from '../../commanComponent/CustomSelect';
import { workExperienceType } from '../../../utils/Constants.config';
import moment from 'moment';
import { calculateFilledPercentage } from '../../../utils/common';
import { CircularProgressbar } from 'react-circular-progressbar';

const WorkExperienceDetails = (props) => {
  const {  addWorkExperienceDetails, profileData, getPreviousObjectKeyValue ,filteredDropdownData,modeProfileModals} = props
  const { t } = useCustomTranslation()
  const [workExperienceDetails, setWorkExperienceDetails] = useState(
    {
      organizationName: '',
      from: '',
      to: '',
      designation: '',
      reasonOfLeaving: '',
    }
  )
  const [tempWorkExperienceDetails, setTempWorkExperienceDetails] = useState([])
  const [isShowDeleteModal, setIsShowDeleteModal] = useState(false)
  const [deleteIndex, setDeleteIndex] = useState('')
  const [workExperience, setWorkExperience] = useState('')
  const percentage = calculateFilledPercentage(workExperienceDetails);


  useEffect(() => {
    if (workExperienceType) {
      setWorkExperience(workExperienceType[0])
    }
  }, [workExperienceType])

  useEffect(() => {
    if (profileData) {
      setTempWorkExperienceDetails(profileData?.work_experience_details)
    }
  }, [profileData])

  useEffect(() => {
    if (workExperience?.value === "fresher") {
      // setTempWorkExperienceDetails([])
      setWorkExperienceDetails(
        {
          organizationName: '',
          from: '',
          to: '',
          designation: '',
          reasonOfLeaving: '',
        })
    }
  }, [workExperience])

  const handleFromChange = (input, fieldName) => {
    const name = fieldName || input.target.name;
    const value = input.target ? input.target.value : input;

    setWorkExperienceDetails((prevData) => {
      let updatedData = {
        ...prevData,
        [name]: value,
      };
      return updatedData;
    });

  };

  const onSaveAndAddForm = async () => {
    setWorkExperienceDetails((pre) => ({
      organizationName: '',
      from: '',
      to: '',
      designation: '',
      reasonOfLeaving: '',
    })
    );
    setTempWorkExperienceDetails((prevData) => [...prevData, workExperienceDetails])
  };

  const onDeleteWorkExperienceDetail = (type) => {
    if (type) {
      const updatedWorkExperienceDetail = tempWorkExperienceDetails.filter((_, i) => i !== deleteIndex);
      setTempWorkExperienceDetails(updatedWorkExperienceDetail);
      setDeleteIndex('')
    }
    toggleDeleteWorkExperienceModal()
  };

  const checkFields = () => {
    const requiredFields = ['organizationName', 'from', 'to', 'designation', 'reasonOfLeaving'];
    const isValid = requiredFields.some(field => workExperienceDetails[field]?.trim() !== '');
    return !isValid;
  }

  const renderWorkExperienceDetails = () => {
    return <div>
      <h6>Details {tempWorkExperienceDetails?.length + 1}</h6>
      <div className="p-3">
        <div className="row">
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={t("labels.workExperience")} required={true} />
              <CustomSelect
                data={workExperienceType}
                placeholder={t("placeholders.workExperience")}
                value={workExperience || ''}
                handleChange={(selectedOption) => setWorkExperience(selectedOption)}
                name="workExperience"
                isDisabled={modeProfileModals}
              />
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={t("labels.organizationName")} />
              <Input
                type="text"
                placeholder={t("placeholders.organizationName")}
                value={workExperienceDetails?.organizationName}
                name="organizationName"
                onChange={(e) => handleFromChange(e)}
                isDisabled={(workExperience?.value === "fresher" ? true : false )||modeProfileModals}
              />
              

            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={t("labels.from")} />
              <Input
                type="date"
                placeholder={t("placeholders.from")}
                value={workExperienceDetails?.from}
                name="from"
                onChange={(e) => handleFromChange(e)}
                isDisabled={(workExperience?.value === "fresher" ? true : false)||modeProfileModals}
              />

            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={t("labels.to")} />
              <Input
                type="date"
                placeholder={t("placeholders.to")}
                value={workExperienceDetails?.to}
                name="to"
                onChange={(e) => handleFromChange(e)}
                isDisabled={(workExperience?.value === "fresher" ? true : false)||modeProfileModals}
                min={workExperienceDetails?.from}
              />

            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={t("labels.designation")} />
              <Input
                type="text"
                placeholder={t("placeholders.enterDesignation")}
                value={workExperienceDetails?.designation}
                name="designation"
                onChange={(e) => handleFromChange(e)}
                isDisabled={(workExperience?.value === "fresher" ? true : false)||modeProfileModals}
              />

            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group mb-3">
              <Label text={t("labels.reasonOfLeaving")} />
              <Input
                type="text"
                placeholder={t("placeholders.reasonOfLeaving")}
                value={workExperienceDetails?.reasonOfLeaving}
                name="reasonOfLeaving"
                onChange={(e) => handleFromChange(e)}
                isDisabled={(workExperience?.value === "fresher" ? true : false)||modeProfileModals}
              />

            </div>
          </div>
        </div>
      </div>

      {(modeProfileModals!==true && modeProfileModals!==false ) &&!(workExperience?.value === "fresher") && <div className="d-flex justify-content-end">
        <CustomButton
          text="Save & Add More"
          classNames="btn btn-primary me-2"
          handleClick={() => onSaveAndAddForm()}
          icon={<Icon className={"fa fa-plus"} />}
          isDisabled={checkFields()}
          
        />
      </div>}

    { <div className='d-flex mt-3 mb-3 p-3' style={{ justifyContent: "space-between" }}>
        {modeProfileModals!==true && modeProfileModals!==false  &&  <div>
          <CustomButton
            text="Back"
            classNames="btn btn-org-gradient"
            handleClick={() => getPreviousObjectKeyValue("short_name","work_experience_details")}

          />
        </div>}
        {modeProfileModals!==true &&<div >
          <CustomButton
            text={modeProfileModals!==true ?"Save":"Save & Next"}
            classNames="btn btn-org-gradient"
            // isDisabled={workExperience?.value === "fresher"?false:true}
            handleClick={() => onHandleSaveWorkExpData()}
          />
        </div>}
      </div>}
    </div>
  }

  const toggleDeleteWorkExperienceModal = (index) => {
    setIsShowDeleteModal(!isShowDeleteModal)
    setDeleteIndex(index)
  }

  const onHandleSaveWorkExpData = () => {
    let payload = {
      work_experience_type: workExperience?.value,
      work_experience_details: tempWorkExperienceDetails?.length && tempWorkExperienceDetails || []
    }
    addWorkExperienceDetails(payload)
  }


  return (
    <>
      <div className={modeProfileModals!==true && modeProfileModals!==false ? `rightSec` : ''}>
        <div className="formPanel detailInner">
          {modeProfileModals!==true && modeProfileModals!==false  &&<div className="DetailHeader">
            <h6 className="m-0 fw-semibold">Work Experience Details</h6>
            <div className="position-relative">
              <div
                id="radialChart3"
                style={{ minWidth: 70, width: 70, height: 70 }}
              />
              <div className="Scoretxt statustxt">
              <div style={{ width: 70, height: 70 }}>
                <CircularProgressbar value={percentage} text={`${percentage}%`} styles={{ path: { stroke: "green" } }} />;
              </div>
              </div>
            </div>
          </div>}

          <div className='rightSec'>
            {tempWorkExperienceDetails && tempWorkExperienceDetails?.length > 0 &&
              <WorkExperienceDetailsTable
                WorkExperienceDetailsData={tempWorkExperienceDetails}
                toggleDeleteWorkExperienceModal={toggleDeleteWorkExperienceModal}
              />
            }
          </div>
          {renderWorkExperienceDetails()}

        </div>
      </div>

      {isShowDeleteModal && (
        <ApproveRejectModal
          onApproveReject={onDeleteWorkExperienceDetail}
          isShow={isShowDeleteModal}
          desc={"You want to delete"}
          btnTextRight={t("btns.delete")}
          btnTextLeft={t("btns.cancel")}
        />)
      }

    </>
  )
}

export default WorkExperienceDetails