import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { onboardingActions } from '../../../core/resource/actions';
import { addFamilyDetailsSchema } from '../../../../src/utils/ValidationSchema';
import { validateSchema } from '../../../../src/utils/ValidationHelper';
import CustomError from '../../globalComponents/CustomError';
import Button from '../../../../src/components/commanComponent/Button'
import { CustomButton } from '../../globalComponents';
import moment from 'moment';
import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import { calculateFilledPercentage } from '../../../../src/utils/common';
import { Input, Label } from '../../commanComponent';
import { useCustomTranslation } from '../../../translation';

const FamilyDetails = (props) => {
  const { t } = useCustomTranslation()
  const { addFamilyDetails, onClickActive, profileData, getPreviousObjectKeyValue,modeProfileModals } = props;
  const [errors, setErrors] = useState({});
  const [familyDetails, setFamilyDetails] = useState(
    {
      "fathers_name": "",
      "fathers_dob": "",
      "mothers_name": "",
      "mothers_dob": "",
      "spouse_name": "",
      "spouse_dob": "",
      "spouse_aadhaar": "",
      "first_child_name": "",
      "first_child_dob": "",
      "second_child_name": "",
      "second_child_dob": "",
    }
  );

  const percentage = calculateFilledPercentage(familyDetails);

  useEffect(() => {
    setFamilyDetails({
      "fathers_name": profileData?.fathers_name || "",
      "fathers_dob": profileData?.fathers_dob || "",
      "mothers_name": profileData?.mothers_name || "",
      "mothers_dob": profileData?.mothers_dob || "",
      "spouse_name": profileData?.spouse_name || "",
      "spouse_dob": profileData?.spouse_dob || "",
      "spouse_aadhaar": profileData?.spouse_aadhaar || "",
      "first_child_name": profileData?.first_child_name || "",
      "first_child_dob": profileData?.first_child_dob || "",
      "second_child_name": profileData?.second_child_name || "",
      "second_child_dob": profileData?.second_child_dob || "",
    });
  }, [profileData])

  const handleFormData = async (e) => {
    const { name, value } = e.target
    setFamilyDetails({ ...familyDetails, [name]: value })
    setErrors(prevErrors => ({
      ...prevErrors,
      [name]: undefined,
    }));
  };

  const onSubmitForm = async () => {
    const validationResponse = await validateSchema(addFamilyDetailsSchema, familyDetails);
    if (validationResponse.isValid) {
      setErrors({});
      addFamilyDetails(familyDetails)
    } else {
      setErrors(validationResponse.errors);
    }
  };

  return (
    <>
      <div className={modeProfileModals!==true && modeProfileModals!==false ?`rightSec`:''}>
      <div className={`formPanel ${modeProfileModals!==true && modeProfileModals!==false ?'detailInner':''}`}>
          {modeProfileModals!==true && modeProfileModals!==false  &&<div className="DetailHeader">
            <div>
              <h6 className="m-0 fw-semibold">Family Details</h6>
              <small>Please fill all family details to proceed to the next step.</small>
            </div>
            <div className="position-relative">
              <div style={{ width: 70, height: 70 }}>
                <CircularProgressbar value={percentage} text={`${percentage}%`} styles={{ path: { stroke: "green" } }} />;
              </div>
            </div>
          </div>}
          <div className={` ${modeProfileModals!==true && modeProfileModals!==false ?'p-3':''}`}>
            <div className="row">
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={t("labels.fatherName")} required={true} />
                  <Input
                    type="text"
                    placeholder={t("placeholders.fatherName")}
                    value={familyDetails?.fathers_name}
                    name="fathers_name"
                    onChange={(e) => handleFormData(e)}
                    isDisabled={modeProfileModals}
                  />

                  {errors.fathers_name && <CustomError
                    text={errors.fathers_name}
                  />}
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={t("labels.fatherDOB")} />
                  <Input
                    type="date"
                    value={familyDetails?.fathers_dob}
                    name="fathers_dob"
                    onChange={(e) => handleFormData(e)}
                    max={moment()
                      .subtract(18, 'years')
                      .format('YYYY-MM-DD')}
                      isDisabled={modeProfileModals}
                  />
                  {errors.fathers_dob && <CustomError
                    text={errors.fathers_dob}
                  />}
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={t("labels.motherName")} required={true} />
                  <Input
                    type="text"
                    value={familyDetails?.mothers_name}
                    name="mothers_name"
                    onChange={(e) => handleFormData(e)}
                    placeholder={t("placeholders.motherName")}
                    isDisabled={modeProfileModals}
                  />

                  {errors.mothers_name && <CustomError
                    text={errors.mothers_name}
                  />}
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={t("labels.motherDOB")} />
                  <Input
                    type="date"
                    value={familyDetails?.mothers_dob}
                    name="mothers_dob"
                    onChange={(e) => handleFormData(e)}
                    max={moment()
                      .subtract(18, 'years')
                      .format('YYYY-MM-DD')}
                      isDisabled={modeProfileModals}
                  />
                  {errors.mothers_dob && <CustomError
                    text={errors.mothers_dob}
                  />}



                </div>
              </div>
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <Label text={t("labels.spouseName")} />
                  <Input
                    type="text"
                    value={familyDetails?.spouse_name}
                    name="spouse_name"
                    onChange={(e) => handleFormData(e)}
                    placeholder={t("placeholders.spouseName")}
                    isDisabled={modeProfileModals}
                  />
                  {errors.spouse_name && <CustomError
                    text={errors.spouse_name}
                  />}

                </div>
              </div>
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <Label text={t("labels.spouseDOB")} />
                  <Input
                    type="date"
                    value={familyDetails?.spouse_dob}
                    name="spouse_dob"
                    onChange={(e) => handleFormData(e)}
                    max={moment()
                      .subtract(18, 'years')
                      .format('YYYY-MM-DD')}
                      isDisabled={modeProfileModals}
                  />
                  {errors.spouse_dob && <CustomError
                    text={errors.spouse_dob}
                  />}

                </div>
              </div>
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <Label text={t("labels.spouseAadharCardNo")} />
                  <Input
                    type="number"
                    value={familyDetails?.spouse_aadhaar}
                    name="spouse_aadhaar"
                    onChange={(e) => handleFormData(e)}
                    placeholder={t("placeholders.spouseAadharCardNo")}
                    isDisabled={modeProfileModals}
                  />
                  {errors.spouse_aadhaar && <CustomError
                    text={errors.spouse_aadhaar}
                  />}
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={t("labels.firstChildName")} />
                  <Input
                    type="text"
                    value={familyDetails?.first_child_name}
                    name="first_child_name"
                    onChange={(e) => handleFormData(e)}
                    placeholder={t("placeholders.firstChildName")}
                    isDisabled={modeProfileModals}
                  />
                  {errors.first_child_name && <CustomError
                    text={errors.first_child_name}
                  />}

                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={t("labels.firstChildDOB")} />
                  <Input
                    type="date"
                    value={familyDetails?.first_child_dob}
                    name="first_child_dob"
                    onChange={(e) => handleFormData(e)}
                    isDisabled={modeProfileModals}

                  />
                  {errors.first_child_dob && <CustomError
                    text={errors.first_child_dob}
                  />}

                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={t("labels.secondChildName")} />
                  <Input
                    type="text"
                    value={familyDetails?.second_child_name}
                    name="second_child_name"
                    onChange={(e) => handleFormData(e)}
                    placeholder={t("placeholders.secondChildName")}
                    isDisabled={modeProfileModals}
                  />
                  {errors.second_child_name && <CustomError
                    text={errors.second_child_name}
                  />}

                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={t("labels.secondChildDOB")} />
                  <Input
                    type="date"
                    value={familyDetails?.second_child_dob}
                    name="second_child_dob"
                    onChange={(e) => handleFormData(e)}
                    isDisabled={modeProfileModals}
                  />
                  {errors.second_child_dob && <CustomError
                    text={errors.second_child_dob}
                  />}
                </div>
              </div>
             { <div className='d-flex' style={{ justifyContent: "space-between" }}>
                {modeProfileModals!==true && modeProfileModals!==false  &&<div className="mt-3 mb-3">
                  <CustomButton
                    text="Back"
                    classNames="btn btn-org-gradient"
                    handleClick={() => getPreviousObjectKeyValue("short_name", "family_details")}
                  />
                </div>}
                {modeProfileModals!==true &&<div className="mt-3 mb-3">
                  <CustomButton
                     text={modeProfileModals !== true?"Save":"Save & Next"}
                    classNames="btn btn-org-gradient"
                    handleClick={() => onSubmitForm()}
                  />
                </div>}
              </div>}
            </div>
          </div>
        </div>
      </div>


    </>
  )
}

export default FamilyDetails