import React, { useEffect, useState } from 'react'
import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import { calculateFilledPercentage } from '../../../../src/utils/common';
import CustomError from '../../globalComponents/CustomError';
import { CustomButton } from '../../globalComponents';
import { validateSchema } from '../../../../src/utils/ValidationHelper';
import { addBankDetailsSchema } from '../../../../src/utils/ValidationSchema';
import { useCustomTranslation } from '../../../translation';
import { Input, Label } from '../../commanComponent';

const BankDetails = (props) => {
  const { t } = useCustomTranslation()
  const { addBankDetails, profileData, getPreviousObjectKeyValue,modeProfileModals } = props;
  const [errors, setErrors] = useState({});
  const [bankDetails, setBankDetails] = useState(
    {
      "ifsc_code": '',
      "bank_name": '',
      "branch_name": '',
      "bank_account_holder_name": '',
      "bank_account_no": '',
      "nominee_name": '',
      "relation_with_nominee": '',
    }
  );

  const percentage = calculateFilledPercentage(bankDetails);

  const handleFormData = async (e) => {
    errors && setErrors({});
    const { name, value } = e?.target
    if (name == 'ifsc_code' && value !== '') {
      let response = await fetch(`https://ifsc.razorpay.com/${value}`);
      const data = await response.json();
      bankDetails['branch_name'] = data?.BRANCH
      bankDetails['bank_name'] = data?.BANK
    }
    setBankDetails({ ...bankDetails, [name]: value })
  };

  const onSubmitForm = async () => {
    const validationResponse = await validateSchema(addBankDetailsSchema, bankDetails);
    if (validationResponse.isValid) {
      setErrors({});
      addBankDetails(bankDetails)
    } else {
      setErrors(validationResponse.errors);
    }
  };

  useEffect(() => {
    if (profileData) {
      setBankDetails(
        {
          "ifsc_code": profileData?.ifsc_code || '',
          "bank_name": profileData?.bank_name || '',
          "branch_name": profileData?.branch_name || '',
          "bank_account_holder_name": profileData?.bank_account_holder_name || '',
          "bank_account_no": profileData?.bank_account_no || '',
          "nominee_name": profileData?.nominee_name || '',
          "relation_with_nominee": profileData?.relation_with_nominee || '',
        }
      );
    }

  }, [profileData])

  return (
    <>
        <div className={modeProfileModals!==true && modeProfileModals!==false  ?`rightSec`:''}>
        <div className={`formPanel ${modeProfileModals!==true && modeProfileModals!==false  ?'detailInner':''}`}>
         {modeProfileModals!==true && modeProfileModals!==false  &&  <div className="DetailHeader">
            <div>
              <h6 className="m-0 fw-semibold">Bank Details</h6>
              <small>Please fill all bank details to proceed to the next step.</small>
            </div>
            <div className="position-relative">
              <div style={{ width: 70, height: 70 }}>
                <CircularProgressbar value={percentage} text={`${percentage}%`} styles={{ path: { stroke: "green" } }} />;
              </div>
            </div>
          </div>}
          <div className={` ${modeProfileModals!==true && modeProfileModals!==false ?'p-3':''}`}>
            <div className="row">
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <Label text={t("labels.ifscCode")} required={true} />
                  <Input
                    type="text"
                    value={bankDetails?.ifsc_code}
                    name="ifsc_code"
                    onChange={(e) => handleFormData(e)}
                    placeholder={t("placeholders.ifscCode")}
                    isDisabled={modeProfileModals}
                  />

                  {errors.ifsc_code && <CustomError
                    text={errors.ifsc_code}
                  />}
                </div>
              </div>
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <Label text={t("labels.bankName")} required={true} />
                  <Input
                    type="text"
                    value={bankDetails?.bank_name}
                    name="bank_name"
                    onChange={(e) => handleFormData(e)}
                    placeholder={t("placeholders.bankName")}
                    isDisabled={modeProfileModals}
                  />

                  {errors.bank_name && <CustomError
                    text={errors.bank_name}
                  />}
                </div>
              </div>
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <Label text={t("labels.branchName")} required={true} />
                  <Input
                    type="text"
                    value={bankDetails?.branch_name}
                    name="branch_name"
                    onChange={(e) => handleFormData(e)}
                    placeholder={t("placeholders.branchName")}
                    isDisabled={modeProfileModals}
                  />
                  {errors.branch_name && <CustomError
                    text={errors.branch_name}
                  />}
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={"Bank Account Holder Name"} required={true} />
                  <Input
                    type="text"
                    name="bank_account_holder_name"
                    value={bankDetails?.bank_account_holder_name}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter Bank Account Holder Name"
                    isDisabled={modeProfileModals}
                  />{errors.bank_account_holder_name && <CustomError
                    text={errors.bank_account_holder_name}
                  />}
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={"Bank Account No."} required={true} />

                  <Input
                    type="number"
                    name="bank_account_no"
                    value={bankDetails?.bank_account_no}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter Bank Account No."
                    isDisabled={modeProfileModals}
                  />{errors.bank_account_no && <CustomError
                    text={errors.bank_account_no}
                  />}
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={"Full Name of Nominee"} required={true} />
                  <Input
                    type="text"
                    name="nominee_name"
                    value={bankDetails?.nominee_name}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter Name of Nominee"
                    isDisabled={modeProfileModals}
                  />{errors.nominee_name && <CustomError
                    text={errors.nominee_name}
                  />}
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <Label text={"Relationship with Nominee"} required={true} />
                  <Input
                    type="text"
                    name="relation_with_nominee"
                    value={bankDetails?.relation_with_nominee}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Select Relationship with nominee"
                    isDisabled={modeProfileModals}
                  />{errors.relation_with_nominee && <CustomError
                    text={errors.relation_with_nominee}
                  />}
                </div>
              </div>
              {<div className='d-flex' style={{ justifyContent: "space-between" }}>
                {modeProfileModals!==true && modeProfileModals!==false  && <div className="mt-3 mb-3">
                  <CustomButton
                    text="Back"
                    classNames="btn btn-org-gradient"
                    handleClick={() => getPreviousObjectKeyValue("short_name", "bank_details")}
                  />
                </div>}
                {modeProfileModals!==true && <div className="mt-3 mb-3">
                  <CustomButton
                    text={modeProfileModals !== true?"Save":"Save & Next"}
                    classNames="btn btn-org-gradient"
                    handleClick={() => onSubmitForm()}
                  />
                </div>}
              </div>}
            </div>
          </div>
        </div>
      </div>

    </>
  )
}

export default BankDetails