import React, { useEffect, useState } from 'react'
import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import { calculateFilledPercentage } from '../../../../src/utils/common';
import CustomError from '../../globalComponents/CustomError';
import { CustomButton } from '../../globalComponents';
import { validateSchema } from '../../../../src/utils/ValidationHelper';
import { addReferenceDetailsSchema } from '../../../../src/utils/ValidationSchema';
import { Input } from '../../commanComponent';

const ReferenceDetails = (props) => {
  const { addReferenceDetails,  profileData, getPreviousObjectKeyValue, modeProfileModals } = props;
  const [errors, setErrors] = useState({});
  const [referenceDetails, setReferenceDetails] = useState(
    {
      "first_reference_name": '',
      "first_reference_no": '',
      "first_reference_email": '',
      "second_reference_name": '',
      "second_reference_no": '',
      "second_reference_email": '',
    }
  );

  const percentage = calculateFilledPercentage(referenceDetails);

  const handleFormData = async (e) => {
    errors && setErrors({});
    const { name, value } = e.target
    setReferenceDetails({ ...referenceDetails, [name]: value })
  };

  const onSubmitForm = async () => {
    const validationResponse = await validateSchema(addReferenceDetailsSchema, referenceDetails);
    if (validationResponse.isValid) {
      setErrors({});
      addReferenceDetails(referenceDetails)
    } else {
      setErrors(validationResponse.errors);
    }
  };


  useEffect(() => {
    setReferenceDetails(
      {
        "first_reference_name": profileData?.reference_details[0]?.first_reference_name || '',
        "first_reference_no": profileData?.reference_details[0]?.first_reference_no || '',
        "first_reference_email": profileData?.reference_details[0]?.first_reference_email || '',
        "second_reference_name": profileData?.reference_details[0]?.second_reference_name || '',
        "second_reference_no": profileData?.reference_details[0]?.second_reference_no || '',
        "second_reference_email": profileData?.reference_details[0]?.second_reference_email || '',
      }
    );
  }, [profileData])

  return (
    <>
      <div className={modeProfileModals!==true && modeProfileModals!==false ? `rightSec` : ''}>
        <div className={`formPanel ${modeProfileModals!==true && modeProfileModals!==false  ?'detailInner':''}`}>
         {modeProfileModals!==true && modeProfileModals!==false  && <div className="DetailHeader">
            <div>
              <h6 className="m-0 fw-semibold">Reference Details</h6>
              <small>Please fill all reference details to proceed to the next step.</small>
            </div>
            <div className="position-relative">
              <div style={{ width: 70, height: 70 }}>
                <CircularProgressbar value={percentage} text={`${percentage}%`} styles={{ path: { stroke: "green" } }} />;
              </div>
            </div>
          </div>}
          <div className={` ${!modeProfileModals?'p-3':''}`}>
            <div className="row">
              <label style={{ color: "darkblue", fontWeight: "bolder" }}>FIRST REFERENCE</label>
              <br />
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <label htmlFor="">Name</label>
                  <Input
                    type="text"
                    name='first_reference_name'
                    value={referenceDetails?.first_reference_name}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter First Reference Name"
                    isDisabled={modeProfileModals}
                  />
                  {errors.first_reference_name && <CustomError
                    text={errors.first_reference_name}
                  />}
                </div>
              </div>
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <label htmlFor="">Contact No.</label>
                  <Input
                    type="number"
                    name='first_reference_no'
                    value={referenceDetails?.first_reference_no}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter First Reference Contact No."
                    isDisabled={modeProfileModals}
                  />
                  {errors.first_reference_no && <CustomError
                    text={errors.first_reference_no}
                  />}
                </div>
              </div>
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <label htmlFor="">Email</label>
                  <Input
                    type="email"
                    name='first_reference_email'
                    value={referenceDetails?.first_reference_email}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter First Reference Email"
                    isDisabled={modeProfileModals}
                  />
                  {errors.first_reference_email && <CustomError
                    text={errors.first_reference_email}
                  />}
                </div>
              </div>
              <br /><br />
              <label style={{ color: "darkblue", fontWeight: "bolder" }}>SECOND REFERENCE</label>
              <br />
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <label htmlFor="">Name</label>
                  <Input
                    type="text"
                    name='second_reference_name'
                    value={referenceDetails?.second_reference_name}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter Second Reference Name"
                    isDisabled={modeProfileModals}
                  />
                  {errors.second_reference_name && <CustomError
                    text={errors.second_reference_name}
                  />}
                </div>
              </div>
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <label htmlFor="">Contact No.</label>
                  <Input
                    type="number"
                    name='second_reference_no'
                    value={referenceDetails?.second_reference_no}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter Second Reference Contact No."
                    isDisabled={modeProfileModals}
                  />
                  {errors.second_reference_no && <CustomError
                    text={errors.second_reference_no}
                  />}
                </div>
              </div>
              <div className="col-md-4">
                <div className="form-group mb-3">
                  <label htmlFor="">Email</label>
                  <Input
                    type="email"
                    name='second_reference_email'
                    value={referenceDetails?.second_reference_email}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter Second Reference Email"
                    isDisabled={modeProfileModals}

                  />
                  {errors.second_reference_email && <CustomError
                    text={errors.second_reference_email}
                  />}
                </div>
              </div>
              <div className='d-flex' style={{ justifyContent: "space-between" }}>
               {modeProfileModals!==true && modeProfileModals!==false  && <div className="mt-3 mb-3">
                  <CustomButton
                    text="Back"
                    classNames="btn btn-org-gradient"
                    handleClick={() => getPreviousObjectKeyValue("short_name", "reference_details")}
                  />
                </div>}
                {modeProfileModals!==true && <div className="mt-3 mb-3">
                  <CustomButton
                    text={modeProfileModals!==true?"Save":"Save & Next"}
                    classNames="btn btn-org-gradient"
                    handleClick={() => onSubmitForm()}
                  />
                </div>}
              </div>
            </div>
          </div>
        </div>
      </div></>
  )
}

export default ReferenceDetails