import React, { useMemo } from 'react'
import { CustomTable } from '../../reuseableComponents'
import moment from 'moment';
import { Icon } from '../../commanComponent';

const WorkExperienceDetailsTable = (props) => {
    const { WorkExperienceDetailsData, toggleDeleteWorkExperienceModal } = props
    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'organizationName', label: 'Organization Name' },
        { key: 'from', label: 'From' },
        { key: 'to', label: 'To' },
        { key: 'designation', label: 'Designation' },
        { key: 'action', label: 'Action' }
    ];

    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span >{index + 1}</span>
        ),

        from: item => (
            <span>
                {item.from ? moment(item.from).format("DD-MM-YYYY") : "-"}
            </span>),
        to: item => (
            <span>
                {item.to ? moment(item.to).format("DD-MM-YYYY") : "-"}
            </span>),
        action: (_, index) => (
            <>
                <span onClick={() => toggleDeleteWorkExperienceModal(index)}>
                    <Icon
                        className={"fa fa-trash text-danger"}
                        style={{ cursor: 'pointer' }}
                    />
                </span>
            </>
        )
    }), [WorkExperienceDetailsData]);
    return (
        <>
            <CustomTable
                data={WorkExperienceDetailsData}
                fields={fields}
                pagination={true}
                tableName={"Employee Details"}
                scopedSlots={scopedSlots}
                isHeader={false}
            />
        </>
    )
}

export default WorkExperienceDetailsTable
