import React, { useState } from 'react';

const FullScreenPreviewModal = ({ isOpen, onClose, fileUrl, fileName, fileType ,isSendMail,isShowSendBtn}) => {
  if (!isOpen) return null;

  return (
    <div
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0, 0, 0, 0.7)',
        zIndex: 1000,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <div
        style={{
          position: 'relative',
          backgroundColor: 'transparent',
          width: '100%',
          height: '90%',
          overflow: 'auto',
        }}
      >
        <button
          style={{
            position: 'fixed',
            top: 10,
            right: 10,
            background: 'transparent',
            border: 'none',
            color: '#fff',
            fontSize: '30px',
            cursor: 'pointer',
          }}
          onClick={onClose}
        >
          ×
        </button>

        {(fileType === 'application/pdf' ||  fileType === 'application/msword' ||
      fileType === 'application/vnd.openxmlformats-officedocument.wordprocessingml.document') ? (
        <>
          <iframe
            src={fileType === 'application/pdf' ? fileUrl : `https://docs.google.com/gview?url=${fileUrl}&embedded=true`}
            title={fileName}
            style={{ border: 'none',width:"100%", height:"90%", background:"transparent",marginTop:"30px" }}
          ></iframe>
          {isShowSendBtn &&<button
          style={{
            position: 'fixed',
            top: 660,
            right: 50,
            background: '#0a53be',
            border: 'none',
            borderRadius:"10px",
            color: '#fff',
            fontSize: '24px',
            cursor: 'pointer',
            padding:"5px 20px"
          }}
          onClick={isSendMail}
        >
          Send
        </button>}
        </>
        ) : (
          <img
            src={fileUrl}
            alt={fileName}
            style={{ width: 'auto', height: 'auto' }}
          />
        )}
      </div>
      
    </div>
  );
};

export default FullScreenPreviewModal;
