import React, { useEffect, useRef, useState } from 'react'
import SignatureCanvas from 'react-signature-canvas'
import { CustomButton } from '../../globalComponents';
import { CircularProgressbar } from 'react-circular-progressbar';
import { calculateFilledPercentage } from '../../../../src/utils/common';
import { useNavigate } from 'react-router-dom';
import moment from 'moment';
import FullScreenPreviewModal from './FullScreenPreviewModal';

const PolicyDetails = (props) => {
  const { allPolicyListData, updatePolicySign, profileData } = props;
  const navigate = useNavigate()
  const sigCanvas = useRef();
  const [userSign, setUserSign] = useState('');
  const [isCollapsedFields, setIsCollapsedFields] = useState('');
  const [loading, setLoading] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [selectedFile, setSelectedFile] = useState(null);

  const percentage = profileData?.accepted_policies ? 100 : Math.round((isCollapsedFields.length / allPolicyListData.length) * 100)

  const trimSign = () => {
    const trimmedCanvas = sigCanvas.current.getTrimmedCanvas();
    const dataURL = trimmedCanvas.toDataURL('image/png');
    setUserSign(dataURL);
  };
  const clearSign = () => {
    sigCanvas.current.clear();
    setUserSign('');
  };

  const handleData = (e, data) => {
    if (e.target.checked) {
      setIsCollapsedFields((prev) => [...prev, data._id]);
    } else {
      setIsCollapsedFields((prev) => prev.filter(id => id !== data._id));
    }
  }

  const onSubmitData = () => {
    let data = {
      employee_id: profileData?._id,
      policyAccepted: true,
      userSign: userSign,
    }
    updatePolicySign(data)
  };

  useEffect(() => {
    setUserSign(profileData?.policy_sign)

  }, [profileData])

  useEffect(() => {
    if (profileData && allPolicyListData) {
      allPolicyListData?.map((item) => {
        if (profileData?.accepted_policies) {
          setIsCollapsedFields((pre) => [...pre, item?._id])
        }
      })
    }

  }, [profileData, allPolicyListData])

  const handlePreviewClick = (fileUrl, fileType, fileName) => {
    setSelectedFile({ fileUrl, fileType, fileName });
    setModalOpen(true);
  };


  return (
    <>
      <div className="rightSec">
        <div className="formPanel detailInner">
          <div className="DetailHeader mb-3">
            <div>
              <h6 className="m-0 fw-semibold">Policy</h6>
              <small>Kindly review all the policies by clicking on each one below</small>
            </div>
            <div className="position-relative">
              <div style={{ width: 70, height: 70 }}>
                <CircularProgressbar value={percentage} text={`${percentage}%`} styles={{ path: { stroke: "green" } }} />;
              </div>
            </div>
          </div>
          <div className="accordion px-3" id="parameteroneFlush">
            {allPolicyListData && allPolicyListData.map((data, index) => (
              <div className="accordion-item mb-3" key={index}>
                <button
                  className="accordion-button parameterName collapsed d-flex justify-content-between align-items-center"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target={`#policy${index}`}
                >
                  <b>{index + 1}.</b>
                  <div className="w-100 parameterinner">
                    <span>
                      Policy Name<strong style={{ textTransform: "capitalize" }}>{data?.title}</strong>
                    </span>
                  </div>
                </button>
                <div
                  id={`policy${index}`}
                  className="accordion-collapse collapse accordion-body py-0"
                  data-bs-parent="#parameteroneFlush"
                >
                  <div className="employeeList mb-0" style={{ position: 'relative', width: '65%' }}>
                    <div
                      onClick={() => handlePreviewClick(data?.file, 'application/pdf', data?.file_name)}
                      style={{
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        width: '100%',
                        height: '100%',
                        zIndex: 2,
                        cursor: 'pointer',
                      }}
                    ></div>

                    <iframe
                      src={data?.file}
                      style={{
                        width: '100%',
                        // height: '300px',
                        border: 'none',
                        transform: 'scale(1.5)',
                        transformOrigin: '0 0',
                        zIndex: 1,
                      }}
                    ></iframe>
                  </div>

                  <div className="toggleSwitch d-flex align-items-center gap-2 ps-4 pb-3" style={{ paddingTop: "80px" }}>
                    <input type="checkbox" id={`switchlogo${index}`}
                      disabled={profileData?.accepted_policies}
                      checked={isCollapsedFields.includes(data?._id)}
                      className="d-none"
                      onChange={(e) => handleData(e, data)} />
                    <label htmlFor={`switchlogo${index}`}>Toggle</label>
                    <strong className="text-primary d-block pb-1">
                      I have thoroughly read the instruction and accepted the policy.
                    </strong>
                  </div>
                </div>
              </div>
            ))}
          </div>
          <div className="p-3 pt-0">
            <div
              className="table-responsive"
              style={{
                padding: 10,
                borderImage: "linear-gradient(to right, #EB3A77 0%, #EF7E37 100%) 1",
                borderRadius: 5,
                borderWidth: 5,
                borderStyle: "solid"
              }}
            >
              <table
                width="100%"
                style={{
                  width: "100%",
                  height: "100%",
                  borderSpacing: 0,
                  backgroundImage: "url(./images/logo-mark.png)",
                  backgroundRepeat: "no-repeat",
                  backgroundSize: 300,
                  padding: 15,
                  backgroundPosition: "center"
                }}
                cellSpacing={0}
              >
                <tbody>
                  <tr>
                    <td>
                      <table style={{ width: "100%" }}>
                        <tbody>
                          <tr>
                            <td
                              align="center"
                              style={{ padding: 15, paddingBottom: 0 }}
                            >
                              <strong
                                style={{
                                  display: "block",
                                  fontSize: 30,
                                  fontWeight: 500,
                                  background:
                                    "-webkit-linear-gradient(#EF7E37, #EB3A77, #A625A7)",
                                  WebkitBackgroundClip: "text",
                                  WebkitTextFillColor: "transparent"
                                }}
                              >
                                <img src="images/qd-service.png" alt="" width={120} />
                              </strong>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <table style={{ width: "100%" }}>
                        <tbody>
                          <tr>
                            <td align="center" style={{ padding: 15, paddingTop: 0 }}>
                              <strong
                                style={{
                                  display: "block",
                                  fontSize: 13,
                                  fontWeight: "bold",
                                  textAlign: "center"
                                }}
                              >
                                <u> BGV AUTHORIZATION CONSENT</u>
                              </strong>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <table style={{ width: "100%" }}>
                        <tbody>
                          <tr>
                            <td align="center" style={{ padding: 15, paddingTop: 8 }}>
                              <strong
                                style={{
                                  display: "block",
                                  fontSize: 12,
                                  fontWeight: 600,
                                  color: "#1D5A7F"
                                }}
                              >
                                To whom It May Concern
                              </strong>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                  </tr>
                  <tr>
                    <td style={{ padding: "0px 14px" }}>
                      <table style={{ width: "100%" }}>
                        <tbody>
                          <tr>
                            <td style={{ padding: 10, width: "60%" }}>
                              <strong
                                style={{
                                  fontSize: 14,
                                  color: "#040404",
                                  fontWeight: "bold"
                                }}
                              >
                                I hereby authorize QDegrees or their representative to
                                verify information presented on my Employee
                                Form/Resume and procure an investigate report or
                                consumer report for{" "}
                              </strong>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                  </tr>
                  <tr>
                    <td style={{ padding: "0px 14px" }}>
                      <table style={{ width: "100%" }}>
                        <tbody>
                          <tr>
                            <td style={{ padding: 10, width: "60%" }}>
                              <strong
                                style={{
                                  fontSize: 14,
                                  color: "#040404",
                                  fontWeight: "bold"
                                }}
                              >
                                I certify that the information furnished in this form
                                filled-in by me in conjunction with my addresses,
                                details are true to the best of my knowledge.
                              </strong>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                  </tr>
                  <tr>
                    <td style={{ padding: "0px 14px" }}>
                      <table style={{ width: "100%" }}>
                        <tbody>
                          <tr>
                            <td style={{ padding: 10, width: "60%" }}>
                              <strong
                                style={{
                                  fontSize: 14,
                                  color: "#040404",
                                  fontWeight: "bold"
                                }}
                              >
                                I hereby grant authority to the bearer of this letter
                                to access or to be provided with full details.{" "}
                              </strong>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                  </tr>
                  <tr>
                    <td style={{ padding: "0px 14px" }}>
                      <table style={{ width: "100%" }}>
                        <tbody>
                          <tr>
                            <td style={{ padding: 10, width: "60%" }}>
                              <strong
                                style={{
                                  fontSize: 14,
                                  fontWeight: "bold",
                                  color: "#000000",
                                  display: "block",
                                  paddingBottom: 10
                                }}
                              >
                                <b style={{ color: "#00376a" }}> Name </b>: {profileData?.name}
                              </strong>
                              <strong
                                style={{
                                  fontSize: 14,
                                  fontWeight: "bold",
                                  color: "#000000",
                                  display: "block",
                                  paddingBottom: 10
                                }}
                              >
                                <b style={{ color: "#00376a" }}> Date </b>: {moment().format("DD-MM-YYYY")}
                              </strong>
                              <strong
                                style={{
                                  fontSize: 14,
                                  fontWeight: "bold",
                                  color: "#000000",
                                  display: "block",
                                  paddingBottom: 10
                                }}
                              >
                                <b style={{ color: "#00376a" }}> Place </b>: {profileData?.location_name}
                              </strong>
                              <strong
                                style={{
                                  fontSize: 14,
                                  fontWeight: "bold",
                                  color: "#000000",
                                  display: "block",
                                  paddingBottom: 10
                                }}
                              >
                                <b style={{ color: "#00376a" }}> Signature </b>
                                : {userSign && <img src={userSign} height="40" width="90" alt="sign" />}
                              </strong>
                              {!profileData?.accepted_policies &&
                                <div
                                  style={{
                                    border: '1px solid',
                                    backgroundColor: 'lightgray',
                                    width: '250px',
                                    height: '150px',
                                  }}
                                >
                                  <SignatureCanvas
                                    penColor="#1d5a7f"
                                    canvasProps={{
                                      width: 250,
                                      height: 150,
                                      className: 'sigCanvas',
                                    }}
                                    ref={sigCanvas}
                                  />
                                </div>
                              }
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                  </tr>
                  {!profileData?.accepted_policies && (<>
                    <div style={{ display: "flex", padding: "20px 20px", gap: "5px" }}>
                      <CustomButton
                        text={"Sign"}
                        classNames="btn btn-primary"
                        handleClick={() => trimSign()}
                      />
                      <CustomButton
                        text={"Clear"}
                        classNames="btn btn-danger"
                        handleClick={() => clearSign()}
                      />
                    </div>
                  </>)}
                  <tr>
                    <td style={{ padding: "5px 24px" }}>
                      <table style={{ width: "100%" }}>
                        <tbody>
                          <tr
                            style={{ borderTop: "1px solid #e7e7e7", paddingTop: 10 }}
                          >
                            <td>
                              <strong
                                style={{
                                  display: "block",
                                  fontWeight: "normal",
                                  textAlign: "center",
                                  paddingBottom: 5,
                                  paddingTop: 20
                                }}
                              >
                                © Copyright (2021) Powered by{" "}
                                <b style={{ fontWeight: "normal", color: "#00376a" }}>
                                  QDegrees
                                </b>
                              </strong>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                  </tr>
                  <tr>
                    <td style={{ padding: "0px 24px" }}>
                      <table style={{ width: "100%" }}>
                        <tbody>
                          <tr>
                            <td>
                              <strong
                                style={{
                                  display: "block",
                                  fontWeight: "normal",
                                  textAlign: "center",
                                  marginBottom: 20
                                }}
                              >
                                <img
                                  src="images/location-address.svg"
                                  alt=""
                                  style={{ paddingRight: 8 }}
                                />
                                B-9, 1st Floor, Mahalaxmi Nagar, Behind WTP South
                                Block, Malviya Nagar, Jaipur, Rajasthan (302017)
                              </strong>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            {!profileData?.accepted_policies && (
              <div className="text-end mt-4 mb-4 d-flex justify-content-end">
                <CustomButton
                  classNames={"btn-org-gradient"}
                  handleClick={() => onSubmitData()}
                  text={"Verify"}
                  isLoading={loading}
                  isDisabled={percentage == 100 ? false : true}
                />

              </div>
            )}
          </div>
        </div>
      </div>
      <FullScreenPreviewModal
        isOpen={modalOpen}
        onClose={() => setModalOpen(false)}
        fileUrl={selectedFile?.fileUrl}
        fileType={selectedFile?.fileType}
        fileName={selectedFile?.fileName}
      />
    </>
  )
}

export default PolicyDetails